.mb-34 {
    margin-bottom: 34px;
}
.mb-26 {
    margin-bottom: 26px;
}
.wrapper {
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;
    height: 100vh;
    flex-direction: column;
}
.form {
    user-select: none;
    display: flex;
    flex-direction: column;
    width: 360px;
    margin: auto;
    background-color: #fff;
    box-shadow:
        rgba(0, 0, 0, 0.25) 0px 54px 55px,
        rgba(0, 0, 0, 0.12) 0px -12px 30px,
        rgba(0, 0, 0, 0.12) 0px 4px 6px,
        rgba(0, 0, 0, 0.17) 0px 12px 13px,
        rgba(0, 0, 0, 0.09) 0px -3px 5px;
    border-radius: 10px;
    padding: 28px;
}
.title {
    color: var(--black);
    font-size: 26px;
    font-weight: 600;
    text-align: center;
    margin-bottom: 34px;
}
.btn {
    position: relative;
    margin-bottom: 70px;
}
.btn-Login,
.btn-Signup {
    text-align: center;
    border-radius: 14px;
    padding: 14px;
    font-size: 16px;
    cursor: pointer;
    position: absolute;
    border: 1px solid black;
    background: white;
    width: 55%;
}
.btn-active {
    background: linear-gradient(-45deg, #043c7c, #0465c5);
    color: white;
}
.btn-Signup {
    right: 0;
}

.email,
.password {
    font-size: 15px;
    font-weight: 600;
    padding: 12px;
    border-radius: 14px;
    outline: none;
    border-top: 1px solid #e1e0e1;
    border-right: 1px solid #e1e0e1;
    border-left: 1px solid #e1e0e1;
    border-bottom: 3px solid #d4d2d4;
}
.password {
    flex: 1;
}
.wrapper-password {
    position: relative;
    height: 45px;
    margin-bottom: 20px;

    input {
        height: 100%;
        padding: 0 0 0 12px;
    }
}
.hidden-password {
    position: absolute;
    right: calc(0% + 32px);
    transform: translateY(calc(50% + 16px));
}
.eye,
.eye-close {
    position: absolute;
    display: flex;
    cursor: pointer;
}
.btn-submit {
    border-radius: 14px;
    padding: 12px;
    color: var(--white);
    font-size: 20px;
    font-weight: 500;
    background: linear-gradient(45deg, #043c7c, #0465c5);
    cursor: pointer;
}
.footer {
    margin-top: 32px;
    font-size: 14px;
    text-align: center;
    font-weight: bold;

    a {
        color: #368bdc;
    }
}
@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}
