.title {
    text-align: center;
    font-size: 20px;
    padding: 24px 0;
    font-weight: 500;
}
.wrapper__video {
    position: relative;
    overflow: hidden;
}

.video {
    padding: 0 50px;
}

.video__clone {
    width: 1350px;
    height: 800px;
}
.btn__next,
.btn__prev {
    font-size: 40px;
    color: var(--black);
    position: absolute;
    top: 40%;
    z-index: 10;
    cursor: pointer;
    transition: all 0.9s linear;
    display: flex;
}
.btn__next {
    right: 24px;
}
.btn__prev {
    left: 24px;
}
.title-colection-slider {
    margin-bottom: 0;
    font-size: 20px;
    font-weight: 500;
    text-align: center;
    margin-top: 32px;
}
.des-colection-silder {
    text-align: center;
    font-weight: 300;
    font-size: 16px;
    margin: 20px 0;
    padding: 0 170px;
    line-height: 24px;
}
